import React from 'react';
import Layout from '../../components/layout';
import styled from 'styled-components';
import movieDB from '../../components/utility/movieDB';

const Styled = styled.section`
  background: var(--bg-dark-gradient);
  margin-top: 90px;
`;

const movie = ({ movies }) => {
  console.log(movies);
  return (
    <Layout>
      <Styled className='movie--section'>Gas</Styled>
    </Layout>
  );
};

movie.getInitialProps = async ({ query }) => {
  console.log(query);
  const queryLeng = Object.keys(query).length;
  try {
    const movies = await movieDB(
      `movie/${queryLeng === 0 ? 'popular' : query.query}`
    );
    return {
      movies: { typeFor: 'movie', ...movies }
    };
  } catch (error) {
    console.log(error);
  }
};

export default movie;
